
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Selector from 'vux/src/components/selector/index.vue'

import { mavonEditor } from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
export default {
name: "create",
components: {
XTextarea,
Group,
XButton,
Selector,
mavonEditor
},
data() {
return {
title: "",
content: "",
value: "请选择",
list: ["请选择", "测试", "分享", "问答", "招聘"],
toolbars: {
bold: true, // 粗体
italic: true, // 斜体
header: true, // 标题
underline: true, // 下划线
quote: true, // 引用
ol: true, // 有序列表
ul: true, // 无序列表
link: true, // 链接
imagelink: true, // 图片链接
code: true, // code
table: true, // 表格
fullscreen: true, // 全屏编辑
htmlcode: true, // 展示html源码
help: true, // 帮助
/* 2.1.8 */
alignleft: true, // 左对齐
aligncenter: true, // 居中
alignright: true, // 右对齐
/* 2.2.1 */
subfield: true, // 单双栏模式
preview: true, // 预览
}
}
},
methods: {

uploadImg:function  (pos,$file) {
var that = this;

console.log(pos);

console.log($file);
var params = new FormData();
params.append("smfile",file)

axios.post("https://sm.ms/api/v2/upload",params,{



headers:{"Content-type":"multipart/form-data"}
}).then(function  (res) {

this.$refs.md.$img2Url(pos,res.data.data.url)
})

},
addContent: function() {
this.$http.post("topics", {
title: this.title,
content: this.content,
tab: "dev",
accesstoken: this.$store.state.accesstoken
}).then(function() {
if(res.data.seccess == true) {
that.$router.replace("/topic/" + res.data.topic_id);
}
})
}
}
}



